<template>
  <div>
    <h2>条形图</h2>
    <div id="chart_example"></div>
  </div>
</template>

<style scoped>
  h2 {
    text-align: center;
    padding: 30px;
    font-size: 18px;
  }
  #chart_example {
    width: 50%;
    height: 500px;
    border: 1px solid red;
    margin: 0 auto;
  }
</style>

<script>
import echarts from 'echarts';

export default {
  name: 'EChartsExample',
  data() {
    return {};
  },
  mounted() {
    this.initChart();
  },
  methods: {
    initChart() {
      const myChart = echarts.init(document.getElementById('chart_example'));
      const option = {
        color: ['#f44'],
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          }
        },
        xAxis: [
          {
            type: 'category',
            data: ['1月', '2月', '3月', '4月', '5月', '6月', '7月', '8月', '9月', '10月', '11月', '12月'],
            axisTick: {
              alignWithLabel: true
            }
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: [
          {
            name: '每月花费',
            type: 'bar',
            barWidth: '60%',
            data: [995, 666, 444, 858, 654, 236, 645, 546, 846, 225, 547, 356]
          }
        ]
      };
      myChart.setOption(option);

      // 监听窗口大小变化，以调整图表大小
      window.addEventListener('resize', () => {
        myChart.resize();
      });
    }
  }
};
</script>
